.sun {
    height: 33px;
    width: 33px;
    cursor: pointer;
    transition: 0.5s ease;
    opacity: 0.5;

    &:hover {
        opacity: 1;
    }
}

.moon {
    height: 33px;
    width: 33px;
    cursor: pointer;
    transition: 0.5s ease;
    opacity: 0.5;

    &:hover {
        opacity: 1;
    }
}

